@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.first {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    height: 88vh;
    position: relative;
}
.mobile-first {
    display: none;
}
.column-image {
    width: 45%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: center;
    height: inherit;
}
.column-text {
    width: 50%;
    display: flex;
    height: 60vh;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
}
.column-text h1 {
    background: #2d1e5c;
    text-transform: uppercase;
    text-align: center;
    color: white;
    width: 100%;
    padding: 10px;
    border-radius: 30px 30px;
}
.logo {
    width: 56%;
}
.button_0_next {
    position: absolute;
    right: 81px;
    bottom: 139px;
    background: #fd005a;
    border-radius: 30px 30px;
    padding: 16px;
    border: none;
    width: 694px;
    cursor: pointer;
    font-size: 0;
}
.button_0_next:hover {
    opacity: 0.8;
}
.button_0_next::before {
    content: "Haz click para controlar tus pulsaciones";
    font-size: 23px;
    color: white;
    font-weight: 500;
}
.button_0_next::after {
    content: "\279C";
    color: white;
    font-size: 23px;
    padding: 0 3px;
    border: 2px solid white;
    border-radius: 67px;
    width: 20px;
    height: 20px;
    margin-left: 10px;
}

@media only screen and (max-width: 1023px) {
    .first {
        display: none;
    }
    .mobile-first {
        display: block;
        padding-top: 24px;
        position: relative;
        height: 85vh;
    }
    .mobile-first h1 {
        position: absolute;
        right: -12px;
        top: 275px;
        text-transform: uppercase;
        width: 50%;
        font-size: 24px;
        text-align: center;
        color: #29235c;
    }
    .mobile-first h1 span {
        color: #bd1622;
    }
    .mobile-first img {
        margin: 0 auto;
        display: block;
    }
    .button_0_next {
        right: 0px;
        bottom: 139px;
        background: #bd1622;
        border-radius: 30px 0 0 30px;
        width: 477px;
    }
    .button_0_next::before {
        font-size: 14px;
    }
    .button_0_next::after {
        font-size: 17px;
        line-height: 18px;
        padding: 0 5px 3px;
        border: 1px solid white;
        background: #bd1622;
        background: linear-gradient(to right, #bd1622 0%, #29235c 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#bd1622', endColorstr='#29235c',GradientType=1 );
        border-radius: 70px;
    }
}
@media only screen and (max-width: 768px) {
    .button_0_next {
        bottom: 287px;
        width: 434px;
        right: 142px;
        border-radius: 30px 30px;
    }
    .button_0_next::before {
        font-size: 17px;
    }
    .manoMobile {
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .mobile-first h1 {
        left: -12px;
    }
}
@media only screen and (max-width: 425px) {
    .button_0_next {
        bottom: 61vh;
        width: 342px;
        right: 0;
        border-radius: 30px 0 0 30px;
    }
    .button_0_next::before {
        font-size: 14px;
    }
    .manoMobile {
        bottom: 17px;
        right: 0;
    }
    .mobile-first h1 {
        top: 40vh;
        right: 0;
        left: auto;
        left: initial;
    }
}

